.phone-country-select {
  flex-shrink: 0;
  width: 8rem;
}

.form-actions {
  display: flex;
  gap: 0.5rem;
}

.form-action-submit,
.form-action-cancel {
  width: auto;
}

@media (max-width: 768px) {
  .utilisateurs-form-title {
    display: none;
  }
  .utilisateurs-form-sidebar {
    display: none;
  }
  .utilisateurs-form-card {
    box-shadow: none;
    border: none;
    border-radius: 0;
    padding: 0;
    background: transparent;
  }
  .phone-country-select {
    width: 7rem;
  }
  .form-actions {
    flex-direction: column;
    gap: 0.75rem;
    padding-top: 0.5rem;
  }
  .form-action-submit {
    width: 100%;
    justify-content: center;
  }
  .form-action-cancel {
    display: none;
  }
}
